// export default Header;

import React from "react";
import logo from "../../assests/images/logo.png";
import logo_black from "../../assests/images/logo_black.png";
import "../../assests/css/Header.css";
import { useNavigate } from "react-router-dom";

const Header = (props) => {
	let navigate = useNavigate();

	return (
		<div className="header">
			<div className="container-fluid">
				<div className="row">
					<div className="col-lg-12">
						<div className="header_full">
							<div className="mr-5 logo-wrapper">
								<a href="/">
									<img
										className="header_logo"
										src={props.black == true ? logo_black : logo}
										alt="main logo"
									/>
								</a>
							</div>
							<div
								className={
									props?.black == true ? "header_links_black " : "header_links"
								}
							>
								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/");
										}}
										className="mb-2"
									>
										Home
									</span>
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/aboutus");
										}}
									>
										About Us
									</span>
								</div>
								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											// navigate("/space-sustainibility");
											navigate("/savenature");
										}}
										className="mb-2"
									>
										Earth Sustainability
									</span>
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/savespace");
										}}
									>
										Space Sustainability
									</span>
								</div>
								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/givers");
										}}
									>
										Givers
									</span>
									<span
										className="mt-2"
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/recievers");
										}}
									>
										Receivers
									</span>
								</div>
								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/partnership");
										}}
										className="mb-2"
									>
										Partners
									</span>
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/recievers");
										}}
									>
										Investors
									</span>
								</div>
								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/community");
										}}
										className="mb-2"
									>
										App
									</span>
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/cryptotoken");
										}}
									>
										Token
									</span>
								</div>

								<div className="hdr_content_spilt">
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/community");
										}}
										className="mb-2"
									>
										Community
									</span>
									<span
										onClick={() => {
											setTimeout(() => {
												window.location.reload();
											}, 100);
											navigate("/blog-categories");
										}}
									>
										Blog
									</span>
								</div>

								<div className="hdr_content_spilt"></div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default Header;
